<template>
    <div class="login-bg">
        <div class="register form-horizontal">
            <h2></h2>
            <div class="form-group has-feedback">
                <label class="control-label col-sm-3" for="inputSuccess3"></label>
                <div class="col-sm-9">
                    <input type="text" v-model="userName" class="form-control" id="inputSuccess3" @change="userNameCheck" placeholder="5~20" aria-describedby="inputSuccess3Status">
                    <span class="glyphicon glyphicon-remove form-control-feedback" 
                    v-show="!reg.userName" aria-hidden="true"></span>
                </div>
                <div class="col-sm-9 warn-msg">
                    <span v-show="!reg.userName"></span>
                </div>
            </div>

            <div class="form-group has-feedback">
                <label class="control-label col-sm-3" for="inputSuccess3"></label>
                <div class="col-sm-9">
                    <input type="email" v-model="email" class="form-control" id="inputSuccess3" @change="emailCheck" placeholder="" aria-describedby="inputSuccess3Status">
                    <span class="glyphicon glyphicon-remove form-control-feedback" v-show="!reg.email" aria-hidden="true"></span>
                </div>
                <div class="col-sm-9 warn-msg">
                    <span v-show="!reg.email"></span>
                </div>
            </div>

            <div class="form-group has-feedback">
                <label class="control-label col-sm-3" for="inputSuccess3"></label>
                <div class="col-sm-9">
                    <input type="password" v-model="password" placeholder="8~20" class="form-control" id="inputSuccess3" @change="passwordCheck" aria-describedby="inputSuccess3Status">
                    <span class="glyphicon glyphicon-remove form-control-feedback" v-show="!reg.password" aria-hidden="true"></span>
                </div>
                <div class="col-sm-9 warn-msg">
                    <span v-show="!reg.password"></span>
                </div>
            </div>

            <div class="form-group has-feedback">
                <label class="control-label col-sm-3" for="inputSuccess3"></label>
                <div class="col-sm-9">
                    <input type="password" class="form-control" id="inputSuccess3" v-model="secpass" @change="secPass" aria-describedby="inputSuccess3Status">
                    <span class="glyphicon glyphicon-remove form-control-feedback" v-show="!reg.secpass" aria-hidden="true"></span>
                </div>
                <div class="col-sm-9 warn-msg">
                    <span v-show="!reg.secpass"></span>
                </div>
            </div>

            <div class="form-group has-feedback">
                <label class="control-label col-sm-3" for="inputSuccess3"></label>
                <div class="col-sm-9">
                    <input type="text" v-model="code" class="form-control security-code" id="inputSuccess3" aria-describedby="inputSuccess3Status">
                    <button type="button" @click="getCode" class="btn btn-primary"></button>
                </div>
            </div>

            <div class="submit">
                <button type="button" @click="jump" class="btn btn-primary"></button>
                <button type="button" @click="onSubmit" class="btn btn-primary"></button>
            </div>
        </div>
    </div>
</template>

<script>
    import FormCheck from '@/validate/index'

    export default {
        name: 'Register',
        data() {
            return {
                userName: '',       // 
                email: '',          // 
                password: '',       // 
                secpass: '',        // 
                code: '',           // 
                
                // 
                reg: {
                    userName: true,
                    email: true,
                    password: true,
                    secpass: true
                }
            }
        },
        methods: {
            // ,
            onSubmit() {

                this.$axios.post('/user/add?uEmail='+this.email+'&uName='+this.userName+'&uPassword='+this.password+'&code='+this.code).then(res => {
                    if(res.data.msg == "") {
                        this.$router.replace('/login');
                    } else {
                        alert("");
                    }
                }).catch(error => {
                    console.log(error);
                });
            },

            // 
            jump() {
                this.$router.push('/login');
            },

            // 
            getCode() {
                this.$axios.post('/user/validate?uEmail='+this.email).then(res => {
                    alert(res.data.msg);
                }).catch(error => {
                    console.log(error);
                });
            },

            // 
            userNameCheck() {
                this.reg.userName = FormCheck.userNameCheck(this.userName);
            },

            // 
            passwordCheck() {
                this.reg.password = FormCheck.passwordCheck(this.password);
            },

            // 
            emailCheck() {
                this.reg.email = FormCheck.checkEmail(this.email);
            },

            // 
            secPass() {
                this.reg.secpass = (this.password == this.secpass ? true : false);
            }
        }
    }   
</script>

<style>
    .login,.register {
        width: 580px;
        border: 1px silver solid;
        box-shadow: 0 0 10px silver;
        margin: 50px auto 0 510px;
        padding: 15px 20px;
        border-radius: 5px;
        background-color: #fff;
    }
    .submit {
        padding-top: 30px;
        text-align: center;
    }
    .warn-msg {
        float: right;
        height: 30px;
        padding-top: 10px;
        color: red; 
    }
    /* .warn-msg span {
        display: none;
    } */
    .security-code {
        display: inline-block;
        width: 60%;
    }
</style>
