<template>
    <div class="sign">
        <div class="sign-data">
            <div class="form-group">
                <input type="text" v-model="input" @change="checkSign" class="form-control sign-num" id="exampleInputEmail1" placeholder="4">
            </div>
            <el-input
            class="sign-text"
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 4}"
            placeholder=""
            @change="checkText"
            v-model="textarea">
            </el-input>
            <el-select v-model="value" clearable placeholder="">
                <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <el-row class="btn-top">
                <el-button type="primary" @click="issueSign"></el-button>
                <el-button @click="goResouce"></el-button>
            </el-row>
        </div>
    </div>
</template>

<script>
import _formCheck from '@/validate/index'

export default {
    data() {
        return {
            input: '',
            options: [{
                value: '2',
                label: '2'
                }, {
                value: '3',
                label: '3'
                }, {
                value: '4',
                label: '4'
                }, {
                value: '5',
                label: '5'
                }],
            value: '',
            textarea: ''
        }
    },
    methods: {
        checkSign() {
            if(!_formCheck.signCheck(this.input)) {
                alert("4");
            }
        },
        checkText() {
            if(!_formCheck.signText(this.textarea)) {
                alert("2~30");
            }
        },
        // 
        issueSign() {
            if(this.value && _formCheck.signCheck(this.input) && _formCheck.signText(this.textarea)) {
                this.$axios.post('/sign/add?mark='+this.input+"&time="+this.value+"&desc="+this.textarea+"&c_id="+this.$route.query.classVal)
                .then(res => {
                    if(res.data.msg == "1") {
                        alert("");
                        this.$router.push({
                            path: '/home/manage-create/sign-list',
                            query: {
                                classVal: this.$route.query.classVal
                            }
                        });
                    } else if(res.data.msg == "") {
                        alert("");
                        this.$router.push({
                            path: '/home/manage-create/sign-list',
                            query: {
                                classVal: this.$route.query.classVal
                            }
                        });
                    } else {
                        alert("");
                    }
                })
                .catch(error => {
                    console.log(error);
                });
            } else {
                alert("");
            }
        },
        // 
        goResouce() {
            this.$router.push({
                path: '/home/manage-create/add',
                query: {
                    classVal: this.$route.query.classVal
                }
            });
        }
    }
}
</script>

<style scoped>
    .sign {
        width: 100%;
        height: 600px;
        padding-bottom: 30px;
        background-color: #f5f5f5;
        border-top: 1px solid #e1e1e1;
        border-bottom: 1px solid #e1e1e1;
    }
    .sign-data {
        width: 980px;
        height: 500px;
        padding: 60px 240px 0;
        background-color: #fff;
        text-align: center;
        margin: 40px auto 0;
    }
    .sign-num {
        text-align: center;
        margin-bottom: 45px;
    }
    .btn-top {
        margin-top: 80px;
        text-align: center;
    }
    .sign-text {
        margin-bottom: 60px;
    }
</style>
